
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
data () {
return {
usableScore: this.$store.state.userScore.usableScore + 300,
list: this.$route.query.list
}
},
components: {
Group,
XButton
},
computed: {
scoreState: function () {
let str
if (this.list.scorePrice < this.usableScore) {
str = '立即兑换'
} else {
str = '积分不足'
}
return str
}

},
methods: {
onChange (val) {
console.log(val)
},
gotodo (val) {
let self = this
self.$vux.confirm.show({
title: '兑换提示',
content: '确定消耗' + val + '积分兑换？',
confirmText: '确认',
cancelText: '取消',
onCancel () {
console.log('cancel')
},
onConfirm () {
console.log('兑换成功')
self.$router.push({path: '/scoreShop'})
}
})
}
}
}



