console.log('start')
function Actor (play) {
  // console.log('ok0')
  //  保存剧本的副本
  this.play = play
  this.play.duration = 10000
  this.play.size = 16
  this.play.color = '#FFF'
  //  给自己化妆
  this.appearance = this.makeUp()
  //  自己走上舞台旁边准备上场
  this.appearance.hide().appendTo($('#barrage'))
}
//  演员化妆，也就是最终呈现的样子
Actor.prototype.makeUp = function () {
  var appearance = $('<div style="min-width:400px;">' + '<img src="" class="userAvator">' + '<p style="background:rgba(0,0,0,0.5);border-radius:10px;display:inline-block;padding-left:10px;padding-right: 10px;font-size:' + this.play.size + ';color:' + this.play.color + ';">' + this.play.nickname + '发了' + this.play.redBagNum + '个红包' + '</p></div>')
  appearance.children('.userAvator').attr('src', this.play.userAvatar).css({
    'width': '20px',
    'height': '20px',
    'position': 'relative',
    'top': '5px',
    'padding-right': '10px'
  })
  return appearance
}
//  演员上场飘过
Actor.prototype.animate = function () {
  var that = this
  var appearance = that.appearance
  var mywidth = appearance.width()
  // console.log('animate')
  // 使用动画控制left
  appearance.animate({
    left: -mywidth
  }, this.play.duration, 'swing', function () {
    appearance.hide()
  })
}
//  演员开始表演
Actor.prototype.perform = function () {
  // console.log('perform')
  var that = this
  var appearance = that.appearance
  // 准备入场,先隐藏在幕布后面
  appearance.css({
    position: 'absolute',
    left: $('#barrage').width() + 'px',
    top: that.play.top||0,
    zIndex: 1,
    display: 'block'
  })
  //  确定入场偏移时间，入场表演
  //  导演只会说第几场开始了，可不会还帮你具体到某个时间点，所以需要你自己计算好入场的时间。
  //  演员需要修正自己上场的时间
  var delayTime = that.play.time - (that.play.session - 1) * self.CheckTime
  setTimeout(function () {
    // console.log('animate-000')
    that.animate()
  }, delayTime)
}

module.exports = Actor



