// console.log('SlideBarrageStart')
let self = this
function SlideBarrage (item) {
  // console.log('ok0')
  //  保存剧本的副本
  this.item = item
  /* 弹幕持续时间 */
  this.item.duration = 8000
  /* 弹幕字体大小 */
  this.item.size = '16px'
  /* 弹幕字体颜色 */
  this.item.color = '#FFF'
  /* 获取单个弹幕 */
  this.appearance = this.makeUp()
  /* 自己走上舞台旁边准备上场 */
  this.appearance.hide().appendTo($('#mom-slide-barrage'))
}
//  演员化妆，也就是最终呈现的样子
SlideBarrage.prototype.makeUp = function () {
  // console.log('演员化妆，也就是最终呈现的样子')
  // console.log(this.item)
  let appearance = $("<div style='min-width:400px;height:30px;line-height:30px;text-align:left;'><img src='' class='userImg'><p class='singleBarrage'>" + this.item.nickname + '赠送' + this.item.materialName + "</p></div>")
  appearance.children('.singleBarrage').css({
    'position': 'absolute',
    'max-width': '80%',
    'display': 'inline-block',
    'border-radius': this.item.size,
    'background': 'rgba(0, 0, 0, 0.5)',
    'font-size': this.item.size,
    'color': this.item.color,
    'padding-left': '10px',
    'padding-right': '10px',
    'overflow': 'hidden',
    'text-overflow':'ellipsis',
    'white-space': 'nowrap'
  })
  appearance.find('.userImg').attr('src', this.item.userAvatar).css({
    'width': '30px',
    'height': '30px',
    'position': 'relative',
    'top': '-2px'
  })
  // console.log(appearance)
  return appearance
}
//  演员上场飘过
SlideBarrage.prototype.animate = function () {
  let that = this
  let appearance = that.appearance
  let screenWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth
  // 使用动画控制left
  appearance.animate({
    left: screenWidth
  }, this.item.duration, 'swing', function () {
    appearance.hide()
  })
}
//  演员开始表演
SlideBarrage.prototype.perform = function () {
  // console.log('perform')
  let that = this
  let appearance = that.appearance
  let mywidth = appearance.width()
  // console.log(that.item.top)
  // 准备入场,先隐藏在幕布后面
  appearance.css({
    position: 'absolute',
    left: -mywidth,
    top: that.item.top,
    zIndex: 100,
    display: 'block'
  })
  that.animate()
}
module.exports = SlideBarrage



